@charset "UTF-8";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

.code {
  font-size: 16px;
  background: #d8d8d8;
  padding: 0 5px;
  border-radius: 5px; }

.link__adalab:hover {
  background-color: #099d8d;
  color: #fff; }

.link__github:hover {
  background-color: #CCC;
  color: #000; }

body {
  padding: 2rem;
  height: 100vh;
  background-image: url("../images/bg-adalab.png");
  background-color: #f3f4f5;
  font-family: "Roboto", sans-serif; }

.page__header {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 3rem; }

.page__footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100vw;
  padding: 1rem;
  color: #999; }

.main__list {
  padding-left: 2rem;
  padding-bottom: 20px; }

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

:root {
  --color-tittle: #14d9c4;
  --color-parraf: #54585a;
  --color-background: rgba(184, 184, 185, 0.2);
  --font-family-Rubik: "Rubik", sans-serif;
  --font-family-OpenSans: "Open sans", sans-serif;
  --color-subtitle: #099d8d;
  --coolgrey: #b8b8b9; }

html {
  font-size: 62.5%; }

.header {
  width: 100%;
  height: 40rem;
  /* display: block; */
  background-image: url("https://images.pexels.com/photos/5706206/pexels-photo-5706206.jpeg?auto=compress&cs=tinysrgb&w=1600");
  background-size: cover; }

.div-header h1 {
  background-color: #099D8D;
  height: 6rem;
  width: 100%;
  opacity: 95%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  font-family: var(--font-family-Rubik);
  font-size: 1.6rem;
  color: white;
  text-transform: uppercase;
  font-weight: 500; }

.nav-header {
  display: flex;
  width: 100%;
  height: 4.5rem;
  font-size: 1.5rem;
  color: white;
  text-transform: uppercase;
  font-weight: 500;
  line-height: 1.88;
  position: fixed;
  top: 0;
  left: 0;
  margin-top: 6rem;
  font-family: var(--font-family-Rubik);
  background-color: #14D9C4;
  justify-content: center;
  z-index: 1;
  opacity: 95%;
  align-items: center;
  padding-right: 1.5rem; }

.ul-header-box {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%; }

.header-box-item {
  list-style: none; }

.div-lema {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  width: 29rem;
  height: 8rem;
  position: sticky;
  top: 80%;
  left: 5%;
  transform: translateY(4rem);
  font-family: var(--font-family-OpenSans);
  font-size: 1.8rem; }

.text-lema {
  width: 26rem;
  height: 5rem; }

.section-team {
  padding-top: 90px;
  padding-bottom: 30px;
  padding-left: 15px;
  padding-right: 15px; }

.team {
  color: var(--color-tittle);
  font-family: var(--font-family-Rubik);
  font-size: 24px;
  text-align: center;
  text-transform: uppercase;
  font-weight: 500; }

.global-presentation {
  font-family: var(--font-family-OpenSans);
  font-size: 16px;
  color: var(--color-parraf);
  text-align: center;
  font-weight: 400; }

.square {
  width: 3rem;
  height: 3rem;
  z-index: 100;
  position: relative;
  left: 50%;
  top: 50%;
  transform: translate(0%, 150%) rotate(45deg);
  padding-bottom: 0%;
  background-color: white;
  border: 2px solid blue; }

.points {
  display: flex;
  flex-direction: column;
  text-align: center;
  line-height: 24px;
  background-color: var(--color-background);
  width: 100%;
  font-family: var(--font-family-OpenSans);
  font-size: 1.6rem;
  color: var(--color-parraf);
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 30px;
  padding-bottom: 45px; }

.points h2 {
  color: var(--color-subtitle);
  text-align: center;
  margin-top: 30px;
  font-family: var(--font-family-Rubik);
  font-size: 22px;
  font-weight: 500;
  margin-bottom: 11.5px;
  margin-top: 11.5px; }

.p-force {
  margin-bottom: 30px; }

.h2-who {
  font-family: var(--font-family-Rubik);
  text-transform: uppercase;
  color: var(--color-subtitle);
  font-weight: 500;
  font-size: 2.4rem;
  text-align: center; }

.description {
  padding-top: 30px;
  padding-bottom: 45px;
  padding-left: 15px;
  padding-right: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center; }

.names {
  font-family: var(--font-family-OpenSans);
  color: black;
  font-size: 2rem;
  line-height: 45px;
  font-weight: 400; }

.description-us {
  font-family: var(--font-family-OpenSans);
  font-size: 1.6rem;
  line-height: 24px;
  color: var(--color-parraf); }

.social-media {
  list-style: none;
  display: flex;
  justify-content: center;
  gap: 5px; }

.icon::after {
  content: "·";
  color: var(--color-subtitle); }

.icon {
  text-decoration: none;
  color: var(--color-subtitle); }

.footer {
  font-family: var(--font-family-OpenSans);
  text-align: center;
  width: 100%;
  height: 25rem;
  padding: 3rem 7.4rem 4.5rem;
  border: solid 1px var(--coolgrey); }

.fnav {
  display: flex;
  justify-content: center; }

.fteam {
  width: 17.2rem;
  height: 2rem;
  margin: 0 0 2rem;
  font-size: 1.4rem;
  color: #b8b8b9;
  line-height: 2rem; }

.flist {
  list-style: none;
  text-transform: uppercase;
  font-size: 1.4rem;
  line-height: 2rem;
  color: #54585a;
  width: 11.4rem;
  height: 6rem;
  margin: 2rem 2.9rem 3rem;
  opacity: 0.75; }

.flogo {
  width: 11.59rem;
  height: 4.5rem;
  margin: 0 2.81rem 0 2.8rem; }

.flogot {
  width: 11.59rem;
  height: 4.5rem;
  display: none; }

@media all and (min-width: 768px) {
  .nav-htablet {
    background-color: black; }
  .footer {
    display: flex;
    justify-content: space-between;
    height: 17rem;
    padding: 2rem 0rem 0rem 0rem; }
  .div-teamlogo {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 50%;
    margin-left: 3rem; }
  .fteam {
    text-align: left; }
  .flogot {
    width: 11.59rem;
    height: 4.5rem;
    display: block;
    justify-content: center;
    padding-top: 1; }
  .fnav {
    width: 50%;
    justify-content: right; }
  .flogo {
    display: none; }
  .flist {
    margin: 0rem 3rem 0rem 0rem;
    height: auto;
    line-height: 3rem;
    text-align: right; } }
